<template>
  <div class="d1">
    <div class="left col-sm-12">
      <criteria-paged :model="model" v-ref:cp>
        <criteria partial='criteria' @condition-changed='search' v-ref:cri>
          <form novalidate class="" partial>
            <div class="col-sm-12">
              <div class="col-sm-1">
                <input type="text" class="form-control" placeholder='客户名称' v-model="model.f_user_name"
                       v-on:keyup.enter="search"
                       condition="f_user_name like '{}%'">
              </div>
              <div class="col-sm-1">
                <input type="text" class="form-control" placeholder='报建编号' v-model="model.f_apply_num"
                       v-on:keyup.enter="search"
                       condition="f_apply_num like '{}%'">
              </div>
              <div class="col-sm-1">
                <input type="text" class="form-control" placeholder='用户地址' v-model="model.f_address"
                       v-on:keyup.enter="search"
                       condition="f_address like '{}%'">
              </div>
              <div class="col-sm-1">
                <input type="text" class="form-control" placeholder='小区名称' v-model="model.f_residential_area"
                       v-on:keyup.enter="search"
                       condition="f_residential_area like '{}%'">
              </div>
              <div class="col-sm-1">
                <v-select  :options="$parent.$parent.usertype" placeholder='用户类型' condition="f_user_type  like '{}%'"
                          v-model="model.f_user_type" :value.sync="model.f_user_type" close-on-select>
                </v-select>
              </div>
              <div class="col-sm-1">
              </div>
              <div class="col-sm-1">
              </div>
              <div class="col-sm-1">
                <button class="btn btn-primary btn-sm width-80" type="button" @click="search(),$parent.$parent.close()">
                  <span class="glyphicon glyphicon-search"></span>查询
                </button>
              </div>
            </div>
          </form>
        </criteria>
        <data-grid :model="model" partial='list' v-ref:grid class="data-grid" is-fixed='false' style="overflow-x:auto">
          <template partial='head'>
            <tr class="title">
              <th>
                <nobr>序号</nobr>
              </th>
              <th>
                <nobr>当前状态</nobr>
              </th>
              <th>
                <nobr>报建编号</nobr>
              </th>
              <th>
                <nobr>客户名称</nobr>
              </th>
              <th>
                <nobr>用户类型</nobr>
              </th>
              <th>
                <nobr>电话</nobr>
              </th>
              <th>
                <nobr>地址</nobr>
              </th>
              <th>
                <nobr>流程状态</nobr>
              </th>
              <th>
                <nobr>报建类型</nobr>
              </th>
              <th>
                <nobr>报建日期</nobr>
              </th>
            </tr>
          </template>
          <template partial='body'>
            <tr>
              <td @click="$parent.$parent.$parent.check(row)">
                <nobr>{{$index+1}}</nobr>
              </td>
              <td @click="$parent.$parent.$parent.check(row)">
                <nobr>{{row.f_sub_state}}</nobr>
              </td>
              <td @click="$parent.$parent.$parent.check(row)">
                <nobr>{{row.f_apply_num}}</nobr>
              </td>
              <td @click="$parent.$parent.$parent.check(row)">
                <nobr>{{row.f_user_name}}</nobr>
              </td>
              <td @click="$parent.$parent.$parent.check(row)">
                <nobr>{{row.f_user_type}}</nobr>
              </td>
              <td @click="$parent.$parent.$parent.check(row)">
                <nobr>{{row.f_phone}}</nobr>
              </td>
              <td @click="$parent.$parent.$parent.check(row)">
                <nobr>{{row.f_address}}</nobr>
              </td>
              <td @click="$parent.$parent.$parent.check(row)">
                <nobr>{{row.defname}}</nobr>
              </td>
              <td @click="$parent.$parent.$parent.check(row)">
                <nobr>{{row.f_apply_type}}</nobr>
              </td>
              <td @click="$parent.$parent.$parent.check(row)">
                <nobr>{{row.f_apply_date}}</nobr>
              </td>
            </tr>
          </template>
        </data-grid>
      </criteria-paged>
    </div>
  </div>
</template>
<script>
  import AppData from '../../../stores/AppData.js'
  import {
    PagedList
  } from 'vue-client'
  import getNowDate from '../../getNowDate.js'

  export default {
    title: '勘探详情',
    data() {
      return {
        model: new PagedList('rs/sql/supervisory', 20, {
          data: {
            id: `${this.$login.f.name}`,
            fengongsi: `${this.$login.f.f_fengongsi}`
          }
        }),
        condition: '',
        select: ''
      }
    },
    ready() {
      this.$refs.cp.$refs.cri.search()
    },
    methods: {
      generateUUID() {
        var d = new Date().getTime()
        var uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
          var r = (d + Math.random() * 16) % 16 | 0
          d = Math.floor(d / 16)
          return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16)
        })
        return uuid
      },
      check(val) {
        this.$dispatch('check', val)
      },
      close() {
        this.$dispatch('close')
      },
      compare(val) {
        val === '完工'
        console.log('完工', val)
        return false
      }
    },
    computed: {
      date() {
        return getNowDate()
      },
      selected() {
        return this.$refs.cp.$refs.grid.selected
      },
      usertype() {
        return [{label: '全部', value: ''}, ...this.$appdata.getParam('用户类型')]
      },
      defnameType() {
        return [{label: '全部', value: ''}, ...this.$appdata.getParam('流程状态')]
      },
      applytype() {
        return [{label: '全部', value: ''}, ...this.$appdata.getParam('报建类型')]
      }
    }
  }
</script>
<style>
  tr.title {
    background-color: #6666ff;
  }


</style>



